.button:hover,
.button:focus {
  outline: none;
  border: 3px solid rgb(166, 199, 218);
  box-shadow: 1px 1px 2px 0 rgb(166, 199, 218);
}

.button {
  border: 2px solid grey;
  display: block;
  margin-top: 10px;
  display: inline-block;
  margin-left: 10px;
}

.list {
  display: inline-block;
}
